import Particles from "react-particles";
import { loadFull } from "tsparticles";
import { useCallback } from "react";

import React from 'react'


const Particle = () => {

  const particlesInit = useCallback(async (engine) => {
    await loadFull(engine)
  }, [])

  const particlesLoaded = useCallback(async (container) => {
    await console.log(container)
  }, [])

  return (
    <Particles
      id="tsparticles"
      init={particlesInit}
      loaded={particlesLoaded}
      options={{
        // background: {
        //     // color: {
        //     //     value: "#0d47a1",
        //     // }
        // },
        fpsLimit: 120,
        interactivity: {
            events: {
                onClick: {
                    enable: true,
                    mode: "push",
                },
                // onHover: {
                //     enable: true,
                //     mode: "repulse",
                // },
                resize: true,
            },
            modes: {
                push: {
                    quantity: 4,
                },
                repulse: {
                    distance: 150,
                    duration: 0.4,
                },
            },
        },
        particles: {
            color: {
                value: "#fff",
            },
            // zIndex: -1,
            links: {
                color: "#ffffff",
                distance: 150,
                enable: true,
                opacity: 0.5,
                width: 1
            },
            move: {
                direction: "top-left",
                enable: true,
                // outModes: {
                //     default: "bounce",
                // },
                random: false,
                // speed: 6,
                straight: false,
            },
            number: {
                density: {
                    enable: true,
                    area: 800,
                },
                value: 80,
            },
            opacity: {
                value: 0.5,
            },
            shape: {
                type: "circle",
            },
            size: {
                // value: { min: 1, max: 2 },
                value: 1
            },
        },
        detectRetina: true,
    }}
    />
  )
}

export default Particle